import { useEffect, useState } from 'react';
import { type ClosePremiumAdBannerMessage } from '@app/messages/premium';
import { PremiumActions } from '@app/actions/premium';
import { versionStringToNumber } from '@popup/shared/helpers/versionStringToNumber';
import { useContentMetaApiData } from '@content/hooks/useContentMetaApiData';
import { browser_api } from '@app/browser_api';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { bridgeStore, useBridgeStore } from '@bridgeStore/index';

export function useBanner(): {
  isShown: boolean;
  handleCloseClick: () => Promise<void>;
} {
  const [isShown, setIsShown] = useState(false);
  const metaApiData = useContentMetaApiData();

  const setData = useBridgeStore((s) => s.setData);

  useEffect(() => {
    async function getIsShown(): Promise<void> {
      const isAdBannerShown =
        bridgeStore.getState()[LocalStorageKey.IsAdBannerShown];

      const currentVersion = browser_api.runtime.getManifest().version;

      const isShown = Boolean(
        metaApiData?.advMinVersion &&
          versionStringToNumber(currentVersion) <=
            versionStringToNumber(metaApiData?.advMinVersion) &&
          isAdBannerShown,
      );

      setIsShown(isShown);
    }

    void getIsShown();
  }, [metaApiData?.advMinVersion]);

  useEffect(() => {
    function listener(changes: {
      isAdBannerShown?: { newValue: boolean };
    }): void {
      if (changes.isAdBannerShown) {
        setIsShown(changes.isAdBannerShown.newValue);
      }
    }

    browser_api.storage.local.onChanged.addListener(listener);
    return () => {
      browser_api.storage.local.onChanged.removeListener(listener);
    };
  }, []);

  async function handleCloseClick(): Promise<void> {
    try {
      await browser_api.runtime.sendMessage<ClosePremiumAdBannerMessage>({
        action: PremiumActions.CloseAdPremiumBanner,
      });
      setData({ isAdBannerShown: false });
    } catch (error) {
      console.error(error);
    }
  }

  return { isShown, handleCloseClick };
}
