import { useEffect, useState } from 'react';
import { useContentMetaApiData } from '@content/hooks/useContentMetaApiData';
import { versionStringToNumber } from '@popup/shared/helpers/versionStringToNumber';
import { browser_api } from '@app/browser_api';
import { AvailableBanner } from '@app/features/types/availableBannersTypes';

export function useAvailableBanner() {
  const [banners, setBanners] = useState<AvailableBanner[]>([]);
  const metaApiData = useContentMetaApiData();

  useEffect(() => {
    async function getIsShown(): Promise<void> {
      if (!metaApiData?.availableBannersData) return;

      const currentVersion = browser_api.runtime.getManifest().version;
      if (
        metaApiData?.advMinVersion &&
        versionStringToNumber(currentVersion) <=
          versionStringToNumber(metaApiData?.advMinVersion)
      ) {
        return;
      }

      const availableBannerValue = Object.values(AvailableBanner);

      const availableBanners: AvailableBanner[] =
        metaApiData?.availableBannersData
          .split(',')
          .filter((banner) =>
            availableBannerValue.includes(banner as AvailableBanner),
          ) as AvailableBanner[];

      setBanners(availableBanners);
    }

    void getIsShown();
  }, [metaApiData?.availableBannersData, metaApiData?.advMinVersion]);

  return banners;
}
