import { FeatureStore } from '@app/features/types/Feature';
import { useMemo } from 'react';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { useBridgeStore } from '@bridgeStore/index';
import { useShallow } from 'zustand/react/shallow';

export const useContentFeaturesList = () => {
  const [store, featuresListLocal] = useBridgeStore(
    useShallow((s) => [
      s[LocalStorageKey.FeatureStore],
      s[LocalStorageKey.FeaturesListLocal],
    ]),
  );

  const featureStore: Partial<FeatureStore> = useMemo(() => {
    if (!store) return {};
    return {
      ...store,
      featuresList: {
        ...store.featuresList,
        ...featuresListLocal,
      },
    };
  }, [store, featuresListLocal]);

  return featureStore;
};
